                :infoTrue="option.infoTrue"
                :fixedBox="option.fixedBox"
            ></vueCropper>
            <div class="operation" v-show="fixedControl">
                <h3>å¯æŒ‰å„ä¸ªå°ºå¯¸è£å‰ª</h3>
                <el-radio-group v-model="fixedType" @change="changeFixedType">
                    <el-radio :label="4">å›¾ç‰‡å°ºå¯¸ï¼ˆè‡ªå®šä¹‰ï¼‰</el-radio>
                    <el-radio :label="1">å›¾ç‰‡å°ºå¯¸ï¼ˆ16:9ï¼‰</el-radio>
                    <el-radio :label="2">å›¾ç‰‡å°ºå¯¸ï¼ˆ4:3ï¼‰</el-radio>
                    <el-radio :label="3">å›¾ç‰‡å°ºå¯¸ï¼ˆ1:1ï¼‰</el-radio>
                </el-radio-group>
