<template>
  <div class="container">
    <div class="wheel-main">
      <div class="wheel-pointer" @click="beginRotate()"></div>
      <canvas width="600" height="400" id="canvas_bg" :style="rotateBgStyle"></canvas>
      <div class="wheel-bg" :style="rotateStyle">
        <div class="prize-list">
          <div
            class="prize-item"
            v-for="(item,index) in prizeList"
            :key="index"
            :style="item.style"
          >
            <div class="prize-pic">
              <img :src="item.icon" />
            </div>
            <div class="prize-type">{{item.name}}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="toast" v-show="prize">
      <div class="toast-container">
        <div
          class="toast-picture"
          :class="prize && prize.isPrize === 1 ? 'congratulation-img' : 'sorry-img'"
        ></div>
        <div class="close" @click="closeToast()"></div>
        <div class="toast-title">{{toastTitle}}</div>
        <div class="toast-btn">
          <div class="toast-cancel" @click="closeToast">å…³é—­</div>
        </div>
      </div>
    </div>
    <div class="toast-mask" v-show="prize"></div>
  </div>
</template>
<script>
import { prizeList } from "./config";
const CIRCLE_ANGLE = 360;

const config = {
  // æ€»æ—‹è½¬æ—¶é—´
  duration: 4000,
  // æ—‹è½¬åœˆæ•°
  circle: 8,
  mode: "ease-in-out"
};

export default {
  name: "lhc-turntable",
  props: {
    // å¥–å“åˆ—è¡¨
    prizeListProp: {
      type: Array,
      default: () => prizeList
    },
    // å‰©ä½™æŠ½å¥–æ¬¡æ•°
    countProp: {
      type: Number,
      default: 3
    }
  },
  data() {
    return {
      count: this.countProp, // å‰©ä½™æŠ½å¥–æ¬¡æ•°
      duration: 3000, // è½¬ç›˜æ—‹è½¬æ—¶é—´
      prizeList: [], // å¥–å“åˆ—è¡¨
      rotateAngle: 0, // æ—‹è½¬è§’åº¦
      index: 0,
      prize: null
    };
  },
  created() {
    // åˆå§‹åŒ–ä¸€äº›å€¼
    this.angleList = [];
    // æ˜¯å¦æ­£åœ¨æ—‹è½¬
    this.isRotating = false;
    // åŸºæœ¬é…ç½®
    this.config = config;

    // èŽ·å–å¥–å“åˆ—è¡¨
    this.initPrizeList();
  },
  computed: {
    /**
     * å¥–å“æ—‹è½¬
     */
    rotateStyle() {
      return `
        -webkit-transition: transform ${this.config.duration}ms ${this.config.mode};
        transition: transform ${this.config.duration}ms ${this.config.mode};
        -webkit-transform: rotate(${this.rotateAngle}deg);
            transform: rotate(${this.rotateAngle}deg);`;
    },
    /**
     * æ—‹è½¬èƒŒæ™¯é¢œè‰²
     * å› ä¸ºå¥–å“å°äºŽ8ç±»çš„æ—¶å€™ï¼Œä¼šæœ‰æ ·å¼ä¸Šçš„é”™ä½ï¼Œæ‰€ä»¥éœ€è¦è§’åº¦this.rotateAngle - this.prizeList.length*5.5
     */
    rotateBgStyle() {
      return `
        -webkit-transition: transform ${this.config.duration}ms ${
        this.config.mode
      };
        transition: transform ${this.config.duration}ms ${this.config.mode};
        -webkit-transform: rotate(${this.rotateAngle -
          this.prizeList.length * 5.5}deg);
            transform: rotate(${this.rotateAngle -
              this.prizeList.length * 5.5}deg);`;
    },
    toastTitle() {
      return this.prize && this.prize.isPrize === 1
        ? "æ­å–œæ‚¨ï¼ŒèŽ·å¾—" + this.prize.name
        : "æœªä¸­å¥–";
    }
  },
  watch: {
    // prizeListProp(data) {
    //   console.log(data);
    // }
  },
  methods: {
    initPrizeList() {
      // è¿™é‡Œå¯ä»¥å‘èµ·è¯·æ±‚ï¼Œä»ŽæœåŠ¡ç«¯èŽ·å–å¥–å“åˆ—è¡¨
      // è¿™é‡Œä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
      this.prizeList = this.formatPrizeList(this.prizeListProp);
    },
    /**
     * åˆå§‹åŒ–ç”»å¸ƒï¼ˆè¯¥è½¬ç›˜èƒŒæ™¯ï¼‰
     */
    initCanvas() {
      var myCanvas = document.querySelector("canvas");
      var ctx = myCanvas.getContext("2d");
      var w = ctx.canvas.width;
      var h = ctx.canvas.height;

      /*åˆ†æˆå‡ ç­‰åˆ†*/
      var num = this.prizeList.length;
      /*ä¸€ä»½å¤šå°‘å¼§åº¦*/
      var angle = (Math.PI * 2) / num;
      // const half = angle / 2;
      /*åŽŸç‚¹åæ ‡*/
      var x0 = w / 2;
      var y0 = h / 2;
      /*èŽ·å–éšæœºé¢œè‰²*/
      var getRandomColor = function() {
        var r = Math.floor(Math.random() * 256);
        var g = Math.floor(Math.random() * 256);
        var b = Math.floor(Math.random() * 256);
        return "rgb(" + r + "," + g + "," + b + ")";
      };
      /*ä¸Šä¸€æ¬¡ç»˜åˆ¶çš„ç»“æŸå¼§åº¦ç­‰äºŽå½“å‰æ¬¡çš„èµ·å§‹å¼§åº¦*/
      var n = this.prizeList.length - 1;
      for (var i = 0; i < num; i++) {
        var startAngle = i * angle;
        var endAngle = (i + 1) * angle;
        ctx.beginPath();
        ctx.moveTo(x0, y0);
        ctx.arc(x0, y0, 150, startAngle, endAngle);
        // è‹¥ä¼ è¿›æ¥æœ‰è®¾ç½®colorï¼Œåˆ™èµ‹å€¼ï¼Œæ— åˆ™éšæœºé¢œè‰²

        if(i===num-1){
          ctx.fillStyle = this.prizeList[i].color
            ? this.prizeList[num-1].color
            : getRandomColor();
        }  else  {
          n--;
          ctx.fillStyle = this.prizeList[i].color
            ? this.prizeList[n].color
            : getRandomColor();
        }

        ctx.fill();
      }
    },
    // æ ¼å¼åŒ–å¥–å“åˆ—è¡¨ï¼Œè®¡ç®—æ¯ä¸ªå¥–å“çš„ä½ç½®
    formatPrizeList(list) {
      // è®°å½•æ¯ä¸ªå¥–çš„ä½ç½®
      const angleList = [];

      const l = list.length;
      // è®¡ç®—å•ä¸ªå¥–é¡¹æ‰€å çš„è§’åº¦
      const average = CIRCLE_ANGLE / l;
      // console.log(average)

      const half = average / 2;

      // å¾ªçŽ¯è®¡ç®—ç»™æ¯ä¸ªå¥–é¡¹æ·»åŠ styleå±žæ€§
      list.forEach((item, i) => {
        // æ¯ä¸ªå¥–é¡¹æ—‹è½¬çš„ä½ç½®ä¸º å½“å‰ i * å¹³å‡å€¼ + å¹³å‡å€¼ / 2
        const angle = -(i * average + half); // + l * 5.5
        // å¢žåŠ  style
        item.style = `-webkit-transform: rotate(${angle}deg);
                      transform: rotate(${angle}deg);`;

        // è®°å½•æ¯ä¸ªå¥–é¡¹çš„è§’åº¦èŒƒå›´
        angleList.push(i * average + half);
      });

      this.angleList = angleList;
      return list;
    },
    beginRotate() {
      // æ·»åŠ æ¬¡æ•°æ ¡éªŒ

      if (this.count === 0) return;

      // å¼€å§‹æŠ½å¥–
      // è¿™é‡Œè¿™é‡Œå‘æœåŠ¡ç«¯å‘èµ·è¯·æ±‚ï¼Œå¾—åˆ°è¦èŽ·å¾—çš„å¥–
      // å¯ä»¥è¿”å›žä¸‹æ ‡ï¼Œä¹Ÿå¯ä»¥è¿”å›žå¥–å“ idï¼Œé€šè¿‡æŸ¥è¯¢ å¥–å“åˆ—è¡¨ï¼Œæœ€ç»ˆå¾—åˆ°ä¸‹æ ‡

      // éšæœºèŽ·å–ä¸‹æ ‡
      this.index = this.random(this.prizeList.length - 1);

      // å‡å°‘å‰©ä½™æŠ½å¥–æ¬¡æ•°
      this.count--;

      // å¼€å§‹æ—‹è½¬
      this.rotating();
    },
    random(max, min = 0) {
      return parseInt(Math.random() * (max - min + 1) + min);
    },
    rotating() {
      const { isRotating, angleList, config, rotateAngle, index } = this;

      if (isRotating) return;

      this.isRotating = true;

      // è®¡ç®—è§’åº¦
      const angle =
        // åˆå§‹è§’åº¦
        rotateAngle +
        // å¤šæ—‹è½¬çš„åœˆæ•°
        config.circle * CIRCLE_ANGLE +
        // å¥–é¡¹çš„è§’åº¦
        angleList[index] -
        (rotateAngle % CIRCLE_ANGLE);

      this.rotateAngle = angle;

      // æ—‹è½¬ç»“æŸåŽï¼Œå…è®¸å†æ¬¡è§¦å‘
      setTimeout(() => {
        this.rotateOver();
      }, config.duration + 1000);
    },
    rotateOver() {
      this.isRotating = false;

      this.prize = this.prizeListProp[this.index];

      this.$emit("draw", [this.prize, this.count]);

      //   console.log(this.prize, this.index, this.count);
    },
    //å…³é—­å¼¹çª—
    closeToast() {
      this.prize = null;
    }
  },
  mounted() {
    this.initCanvas();
  }
};
</script>
<style scoped>
.container {
  width: 100%;
}
.wheel-main {
  margin: 0 auto;
  position: relative;
  width: 295px;
  height: 295px;
}
.wheel-bg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  /* background: url("../assets/img/draw_wheel.png") no-repeat center top; */
  background-size: 100%;
  color: #fff;
}
.wheel-pointer {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  width: 85px;
  height: 85px;
  background: url("./../../../examples/assets/img/draw_btn.png") no-repeat
    center top;
  background-size: 100%;
  transform: translate3d(-50%, -50%, 0);
}
.wheel-bg div {
  text-align: center;
}
.prize-list {
  width: 100%;
  height: 100%;
  position: relative;
}
.prize-list .prize-item {
  position: absolute;
  width: 95px;
  height: 150px;
  top: 0;
  left: 50%;
  margin-left: -47.5px;
  transform-origin: 50% 100%;
}

.prize-pic img {
  width: 4.0625rem;
  height: 2.5rem;
  margin-top: 1.5625rem;
}
.prize-count {
  font-size: 0.875rem;
}
.prize-type {
  font-size: 0.75rem;
}
.toast-mask {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 10000;
  width: 100%;
  height: 100%;
}
.toast {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 20000;
  transform: translate(-50%, -50%);
  width: 15.4375rem;
  background: #fff;
  border-radius: 0.3125rem;
  padding: 0.3125rem;
  background-color: rgb(252, 244, 224);
}
.toast-container {
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px dotted #fccc6e;
}
.toast-picture {
  position: absolute;
  top: -6.5rem;
  left: -1.5rem;
  width: 18.75rem;
  height: 8.5625rem;
}
.congratulation-img {
  background: url("./../../../examples/assets/img/congratulation.png") no-repeat
    center top;
  background-size: 100%;
}
.sorry-img {
  background: url("./../../../examples/assets/img/sorry.png") no-repeat center
    top;
  background-size: 100%;
}
.toast-pictrue-change {
  position: absolute;
  top: -1.5rem;
  left: -1.375rem;
  width: 17.5rem;
  height: 3.125rem;
}
.toast-title {
  padding: 2.8125rem 0;
  font-size: 18px;
  color: #fc7939;
  text-align: center;
}
.toast-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.9375rem;
}
.toast-btn div {
  background-image: -moz-linear-gradient(
    -18deg,
    rgb(242, 148, 85) 0%,
    rgb(252, 124, 88) 51%,
    rgb(252, 124, 88) 99%
  );

  background-image: -ms-linear-gradient(
    -18deg,
    rgb(242, 148, 85) 0%,
    rgb(252, 124, 88) 51%,
    rgb(252, 124, 88) 99%
  );
  background-image: -webkit-linear-gradient(
    -18deg,
    rgb(242, 148, 85) 0%,
    rgb(252, 124, 88) 51%,
    rgb(252, 124, 88) 99%
  );
  box-shadow: 0px 4px 0px 0px rgba(174, 34, 5, 0.7);
  width: 4.6875rem;
  height: 1.875rem;
  border-radius: 1.875rem;
  text-align: center;
  line-height: 1.875rem;
  color: #fff;
}
.close {
  position: absolute;
  top: -0.9375rem;
  right: -0.9375rem;
  width: 2rem;
  height: 2rem;
  background: url("./../../../examples/assets/img/close_store.png") no-repeat
    center top;
  background-size: 100%;
}

canvas {
  position: absolute;
  left: -153px;
  top: -47px;
}
</style>

